      </template>
      <template v-slot:[`item.scale_min`]="{ item }">
        <v-edit-dialog
          :return-value.sync="item.scale_min"
          @save="updatePoint(item)"
          @cancel="cancelEdit"
          @open="openEdit"
          @close="closeEdit"
          large
        >
          <b>{{ item.scale_min }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.scale_min" single-line type="number" min="0" @input="numberChange()" />
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:[`item.scale_max`]="{ item }">
        <v-edit-dialog
          :return-value.sync="item.scale_max"
          @save="updatePoint(item)"
          @cancel="cancelEdit"
          @open="openEdit"
          @close="closeEdit"
          large
        >
          <b>{{ item.scale_max }}</b>
          <template v-slot:input>
            <v-text-field v-model="item.scale_max" single-line type="number" min="0" @input="numberChange()" />
          </template>
        </v-edit-dialog>
      </template>

      <template v-slot:[`header.enable`]>
        {{ toUpperTextKey('enable') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('enable'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.cov_threshold`]>
        {{ toUpperTextKey('cov_threshold') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('cov_threshold'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.value_operation`]>
        {{ toUpperTextKey('value_operation') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('value_operation'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.value_round`]>
        {{ toUpperTextKey('value_round') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('value_round'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.input_min`]>
        {{ toUpperTextKey('input_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('input_min'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.input_max`]>
        {{ toUpperTextKey('input_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('input_max'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.scale_min`]>
        {{ toUpperTextKey('scale_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_min'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>
      <template v-slot:[`header.scale_max`]>
        {{ toUpperTextKey('scale_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_max'), $event.stopPropagation()" color="primary"
          >mdi-pencil</v-icon
        >&nbsp;
      </template>

      <template v-slot:no-data> No data</template>
    </v-data-table>

    <!-- Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="device.name" disabled class="centered-input" />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.name"
                    label="Point name"
                    required
                    :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"
                    @input="val => (editedItem.name = conventionNaming(val, setting))"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field v-model="editedItem.value_operation" label="math operation" />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.cov_threshold"
                    :label="toUpperTextKey('cov_threshold')"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.value_round"
                    :label="toUpperTextKey('value_round')"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.input_min"
                    :label="toUpperTextKey('input_min')"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.input_max"
                    :label="toUpperTextKey('input_max')"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.scale_min"
                    :label="toUpperTextKey('scale_min')"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  />
                </v-col>
                <v-col cols="12" sm="4" md="4">
                  <v-text-field
                    v-model="editedItem.scale_max"
                    :label="toUpperTextKey('scale_max')"
                    type="number"
                    min="0"
                    @input="numberChange()"
                  />
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save()" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            v-model.number="editAll[editField]"
            min="0"
            :label="toUpperTextKey(editField)"
            type="number"
            @input="numberChange()"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'string'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, POINT_HEADERS, POINT_ADVANCED_HEADERS, POINT_EDIT_ALL_DEFAULT } from './loraConstant';
import {
  copyToClipboard,
  exportJSON,
  exportCSV,
  exportExcel,
  toUpperTextKey,
  conventionNaming,
} from '../../utils/utils';

export default {
  components: {
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    conventionNaming: conventionNaming,
    rules: { ...RULES },
    tableProps: { ...DATA_TABLE_PROPS },
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    editAll: { ...POINT_EDIT_ALL_DEFAULT },
    dialog: false,
    dialogMassEdit: false,
    device: {},
    selected: [],
    dirs: [],
    file: null,
    search: '',
    searchItems: [],
    valid: false,
    editedIndex: -1,
    editedItem: {},
    editField: '',
    intervalId: null,
    advancedView: false,
  }),
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {},
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getItems();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getItems();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    async getItems() {
      await axios.get(`lora/device/${this.$route.params.id}`).then(data => {
        this.device = data;
        this.device.points = data.points.map(item => {
          const keys = Object.keys(item.point_store);
          if (keys && keys.length > 0) {
            keys.forEach(key => {
              item = { ...item, [key]: item.point_store[key] };
            });
          }
          delete item.point_store;
          delete item.point_uuid;
          return item;
        });

        //get dir
        if (this.dirs.length != 2) {
          (this.dirs = [{ text: 'Devices', link: '/lora-network/devices', disabled: false }]),
            this.dirs.push({ text: this.device.name, disabled: true });
        }
      });
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      if (type === 'csv') {
        return exportCSV('LoRa Networks - Points', array);
      }
      if (type === 'excel') {
        return exportExcel('LoRa Networks - Points', array);
      }
      if (type === 'json') {
        return exportJSON('LoRa Networks - Points', JSON.stringify(array));
      }
    },
    updatePoint(point) {
      axios
        .patch(`lora/point/${point.uuid}`, point)
        .then(response => {
          const index = this.device.points.findIndex(p => p.uuid == point.uuid);
          Object.assign(this.device.points[index], point);
          this.$toasted.show('update success');
        })
        .catch(error => {
          this.$toasted.show('update fail', { type: 'error' });
        });
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updatePoint(this.editedItem);
        } else {
          // this.createPoint(this.editedItem);
        }
        this.close();
      }
    },
    isExistingvalue(value, fieldName) {
      let points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      return _find(points, { [fieldName]: value }) != null;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...POINT_EDIT_ALL_DEFAULT };
    },
    numberChange() {
      if (!this.editAll[this.editField]) return (this.editAll[this.editField] = 0);
    },
    cancelEdit() {},
    openEdit() {},
    closeEdit() {},
    onClickRefresh() {
      this.getItems();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
